} */
.container {
  display: flex;
  width: 100%;
  height: 90vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.quantity {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}
.quantity__minus,
.quantity__plus {
  display: block;
  width: 60px;
  height: 53px;
  margin: 0;
  background: #b8c0fa;
  text-decoration: none;
  text-align: center;
  line-height: 48px;
  font-size: 60px;
}
.quantity__minus:hover,
.quantity__plus:hover {
  background: #575b71;
  color: #fff;
}
.quantity__minus {
  border-radius: 3px 0 0 3px;
}
.quantity__plus {
  border-radius: 0 3px 3px 0;
}
.quantity__input {
  width: 70px;
  height: 50px;
  margin: 0;
  padding: 0;
  text-align: center;
  border-top: 2px solid #b8c0fa;
  border-bottom: 2px solid #b8c0fa;
  border-left: 1px solid #b8c0fa;
  border-right: 2px solid #b8c0fa;
  background: #fff;
  color: #8184a1;
  font-size: 20px;
}
.quantity__minus:link,
.quantity__plus:link {
  color: #8184a1;
}
.quantity__minus:visited,
.quantity__plus:visited {
  color: #fff;
}